@import url("https://fonts.googleapis.com/css?family=Lato:400,700");
.heroImage {
  align-items: center;
  background: radial-gradient(ellipse at center, rgba(0, 0, 0, 0.5) 0%, transparent 100%), url("https://choozle.com/img/homepage/homepage-hero-bg.jpg") no-repeat scroll center;
  display: flex;
  height: 600px;
  justify-content: center;
  position: relative;
  width: 100%; }
  @media screen and (min-width: 1025px) {
    .heroImage {
      background-attachment: fixed; } }

.form {
  margin-top: 60px; }
  .form__title {
    color: #FFF;
    font-family: "Lato", sans-serif;
    font-size: 24px;
    font-weight: bold;
    line-height: 1.5em;
    margin: 20px 0;
    padding: 0 10px;
    text-align: center;
    text-shadow: 3px 2px 8px #414042; }
    @media screen and (min-width: 770px) {
      .form__title {
        font-size: 38px; } }
  .form__content {
    color: #FFF;
    font-family: "Lato", sans-serif;
    font-size: 13px;
    margin-top: 30px;
    text-align: center; }
    .form__content span {
      text-decoration: underline; }

.banner {
  align-items: center;
  background-color: #7cad42;
  display: flex;
  height: 80px;
  justify-content: center; }
  .banner__title {
    color: #FFF;
    font-size: 20px;
    font-weight: bold;
    line-height: 1.3em;
    text-align: center; }
    @media screen and (min-width: 1025px) {
      .banner__title {
        font-size: 32px; } }

.modal {
  align-items: center;
  display: flex;
  flex-direction: column;
  justify-content: center; }
  .modal__content {
    font-family: "Lato", sans-serif;
    font-size: 18px;
    line-height: 1.3em;
    margin: 30px 0;
    text-align: center; }

.content {
  height: 800px; }



