@import url("https://fonts.googleapis.com/css?family=Lato:400,700");
.form {
  align-items: center;
  display: flex;
  flex-direction: column;
  justify-content: center; }

.floatLabel {
  display: block;
  margin-bottom: 10px;
  position: relative; }
  .floatLabel label, .floatLabel > span {
    color: #777;
    cursor: text;
    font-size: 75%;
    left: 0;
    padding-top: 5px;
    position: absolute;
    opacity: 1;
    text-indent: 10px;
    top: 0;
    transition: all .2s; }
  .floatLabel input {
    border: none;
    border-bottom: 2px #7cad42 solid;
    border-radius: 5px;
    font-size: 18px;
    height: 42px;
    padding-top: 20px;
    text-indent: 10px;
    width: 300px; }
    .floatLabel input::placeholder {
      font-size: 14px;
      opacity: 0;
      transition: all .2s; }
    .floatLabel input:placeholder-shown:not(:focus)::placeholder {
      opacity: 0; }
    .floatLabel input:placeholder-shown:not(:focus) + * {
      font-size: 100%;
      opacity: 0.7;
      padding-top: 8px;
      text-indent: 10px;
      top: .25em; }
    .floatLabel input:focus {
      border-color: #628834;
      outline: none; }



