@import url("https://fonts.googleapis.com/css?family=Lato:400,700");
.label {
  color: #777;
  font-family: "Lato", sans-serif;
  font-size: 13px; }

.red {
  background-color: #FFF;
  border: 3px #ff694a solid;
  border-radius: 5px;
  margin: 0 20px 0 10px;
  outline-color: #ffd3ca;
  padding: 10px 0;
  transition: background-color 0.5s;
  width: 120px; }
  .red:hover, .red:focus, .red:active {
    background-color: #ff694a;
    cursor: pointer; }
    .red:hover .label, .red:focus .label, .red:active .label {
      color: #FFF; }

.red-solid {
  background-color: #ff694a;
  border: none;
  border-radius: 5px;
  outline-color: #ffd3ca;
  padding: 10px;
  transition: background-color 0.5s; }
  .red-solid .label {
    color: #FFF;
    font-size: 20px; }
  .red-solid:hover, .red-solid:focus, .red-solid:active {
    background-color: #ff4721;
    cursor: pointer; }

.green {
  background-color: #FFF;
  border: 3px #7cad42 solid;
  border-radius: 5px;
  outline-color: #bad797;
  padding: 10px 0;
  transition: background-color 0.5s;
  width: 120px; }
  .green:hover, .green:focus, .green:active {
    background-color: #7cad42;
    cursor: pointer; }
    .green:hover .label, .green:focus .label, .green:active .label {
      color: #FFF; }



